"use client";

import { useState } from "react";

// Role-specific questions
const QUESTIONS = {
  frontend: [
    "Explain the virtual DOM in React.",
    "What are hooks and how do you use useEffect?",
  ],
  backend: [
    "Explain REST vs GraphQL.",
    "What is database indexing and why is it important?",
  ],
};

// Simple AI improvement tips simulator
function getAITips(role, wrongAnswers) {
  const tips = {
    frontend: [
      "Review React documentation for hooks.",
      "Practice component optimization techniques.",
    ],
    backend: [
      "Review database indexing strategies.",
      "Practice designing REST APIs efficiently.",
    ],
  };
  return tips[role].slice(0, wrongAnswers.length);
}

export default function MockInterview() {
  const [role, setRole] = useState("frontend");
  const [current, setCurrent] = useState(0);
  const [answers, setAnswers] = useState([]);
  const [showResult, setShowResult] = useState(false);

  const handleAnswer = (answer) => {
    setAnswers([...answers, answer]);
    if (current < QUESTIONS[role].length - 1) {
      setCurrent(current + 1);
    } else {
      setShowResult(true);
    }
  };

  const wrongAnswers = answers.filter((a) => a.trim() === "");
  const tips = getAITips(role, wrongAnswers);

  return (
    <div style={{ maxWidth: "600px", margin: "50px auto", fontFamily: "sans-serif" }}>
      <h1>Mock Interview Simulator</h1>

      {!showResult ? (
        <div>
          <label>
            Select Role:{" "}
            <select
              value={role}
              onChange={(e) => {
                setRole(e.target.value);
                setCurrent(0);
                setAnswers([]);
                setShowResult(false);
              }}
            >
              <option value="frontend">Frontend</option>
              <option value="backend">Backend</option>
            </select>
          </label>

          <div style={{ marginTop: "20px" }}>
            <h3>Question {current + 1}:</h3>
            <p>{QUESTIONS[role][current]}</p>
            <input
              type="text"
              placeholder="Your answer..."
              value={answers[current] || ""}
              onChange={(e) => {
                const newAns = [...answers];
                newAns[current] = e.target.value;
                setAnswers(newAns);
              }}
              style={{ width: "100%", padding: "8px", marginTop: "10px" }}
            />
            <button
              onClick={() => handleAnswer(answers[current] || "")}
              style={{ marginTop: "10px", padding: "8px 16px" }}
            >
              Next
            </button>
          </div>
        </div>
      ) : (
        <div>
          <h2>Performance Summary</h2>
          <p>
            Correct Answers: {answers.length - wrongAnswers.length} / {QUESTIONS[role].length}
          </p>

          {tips.length > 0 && (
            <div>
              <h3>AI Improvement Tips:</h3>
              <ul>
                {tips.map((tip, i) => (
                  <li key={i}>{tip}</li>
                ))}
              </ul>
            </div>
          )}

          <button
            onClick={() => {
              setCurrent(0);
              setAnswers([]);
              setShowResult(false);
            }}
          >
            Restart
          </button>
        </div>
      )}
    </div>
  );
}
